import IReportRow from "./report-row";

export function ratingText(row: IReportRow): string {
    switch (row.rating) {
        case 6: return "AAA";

        case 5.1: return "AA+";
        case 5.0: return "AA";
        case 4.9: return "AA-";

        case 4.1: return "A+";
        case 4.0: return "A";
        case 3.9: return "A-";

        case 3.1: return "BBB+";
        case 3.0: return "BBB";
        case 2.9: return "BBB-";

        case 2.1: return "BB+";
        case 2.0: return "BB";
        case 1.9: return "BB-";

        case 1.1: return "B+";
        case 1.0: return "B";
        case 0.9: return "B-";
    }
    return "";
}

export function couponHintText(row: IReportRow) {
    switch (row.couponType) {
        case 1: return "↓";
        case 2: return "**";
        case 3: return "оф";
    }
    return "";
}

export function toTsvLine(row: IReportRow) {
    const cells = [
        row.isin,
        pad(row.name, 12),
        tags(),
        row.listLevel,
        pad(ratingText(row), 4),
        row.maturityDate,
        row.hasFutureAmort ? "A" : "",
        numeric(row.waprice),
        couponHintText(row),
        numeric(row.couponValues[0]),
        numeric(row.closeYield)
    ];

    function tags(): string {
        const list = [ ];

        if (row.isQualOnly) {
            list.push("кв");
        } else if (row.inABI) {
            list.push("*");
        } else if (row.inCBHY) {
            list.push("вд");
        }

        if (row.isHighRisk) {
            list.push("пир");
        }

        return pad(list.join(", "), 7);
    }

    function numeric(value: number): string {
        return pad(String(value || ""), 8, true);
    }

    function pad(text: string, len: number, left = false): string {
        while (text.length < len) {
            if (left) {
                text = " " + text;
            } else {
                text = text + " ";
            }
        }
        return text;
    }

    return cells.join("\t");
}
